body{
    background-color: rgb(46 69 75);
}

.app{
    text-align: center;
}

.container{
    background-color: rgb(232 76 104);
    justify-content: center;
    width: 50%;
    margin-left: 24%;
    margin-top: 5%;
    padding: 12px;
    text-align: center;
    font-weight: bold;
    margin-bottom: 5%;
}

.header{
    width: 100%;
    padding: 7px 0px;
    color: white;
    font-size: 50px;

}

input[type=text], select{
    width: 80%;
    padding: 10px 15px;
    margin: 18px 8px;
    display: inline-block;
    border: 3px solid #ccc;
    border-radius: 4 px;
    box-sizing: border-box;
   
}

button:hover{
   
    background-color: rgb(165 239 165);
}

button{
    width: 14%;
    /* background-color: rgb(92, 206, 92); */
    color: black;
    padding: 10px 4px;
    margin: 4px 2px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-weight: bold;
}

.listHeading{
    color: #f2e1e1;
    font-size: 27px;
    text-align: center;
    background-color: #272727;
    width: 70%;
    padding: 5px 6px;
    margin: 8px 52px;
}

.listData{
    color: black;
    font-size: 20px;
    background-color: rgb(246 238 212);
    text-align: left;
    padding-left: 15px;
    width: 48%;
    padding-bottom: 2%;
    padding-top: 2%;
    border-radius: 5px;
}

.btn-position{
    position: relative;
    left: 11%;
    top: -54px;
}